









































import {Vue, Component} from 'vue-property-decorator';
import {Panel, Button, CellGroup, Cell, Toast} from 'vant';
import {notes, NoteType} from '../../util/note';
import 'vant/lib/panel/style';
import 'vant/lib/button/style';
import 'vant/lib/cell/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/toast/style';
import ScanSign from '../../components/scan_sign.vue';
import {
    AuthOriginalTxModel, AuthAdminType, default as OriginalAddressModel
} from '../../model/original_address_model';
import {CashboxJs} from '../../util/cashboxjs';
import {DiamondUtil} from "../../util/diamond_util";
import {ToastUtil} from "../../util/toast";
import {ExtrinsicStatus} from "@polkadot/types/interfaces";

Vue.use(Panel).use(Button).use(Cell).use(CellGroup).use(Toast);

@Component({
    components: {ScanSign},
})

export default class Confirm extends Vue {
    private authTypeValue = "";             // 授权或取消
    private isLoadingAccount = true;
    private txAddress = "";                 // 操作申请者 地址
    private txName = "";                    // 操作申请者 用户名
    private nowApplyAddress = '';           // 授权管理员 地址
    private waitSignInfo = '';              // 拼接 待签名信息
    private isShowLoading = false;

    public created(): void {
        this.$store.state.title = '信息签名';
        {
            (window as any).nativeSignMsgToJsResult = this.nativeSignMsgToJsResult;
        }
        if (this.$store.state.authOriginalTxModel.authType == AuthAdminType.DeleteAdmin) {
            this.authTypeValue = this.$t('cancelOriginal').toString();
        } else {
            this.authTypeValue = this.$t('authOriginal').toString();
        }
    }

    public async mounted() {
        const authOriginalTxModel: AuthOriginalTxModel = this.$store.state.authOriginalTxModel;
        this.nowApplyAddress = authOriginalTxModel.nowAdminAddress;
        console.log("confirm page this.nowApplyAddress===>" + authOriginalTxModel.nowAdminAddress + "||" + this.nowApplyAddress);
        this.txAddress = authOriginalTxModel.txAddress;
        this.txName = authOriginalTxModel.txName;
        await this.loadWaitSignInfo();
        this.isLoadingAccount = false;
    }

    public async loadWaitSignInfo() {
        console.log("this.$store.state.authOriginalTxModel.authType is===>" + this.$store.state.authOriginalTxModel.authType);
        this.isShowLoading = true;
        this.waitSignInfo = await DiamondUtil.getInstance().assembleAdminSingleMessage(this.$store.state.authOriginalTxModel.authType,
            this.$store.state.nativeChainAddress, this.txAddress, this.txName);
        console.log("this.waitSignInfo is:===》" + this.waitSignInfo);
        this.isShowLoading = false;
        if (!this.waitSignInfo || this.waitSignInfo === "") {
            this.waitSignInfo = "";
            ToastUtil.show("拼装数据失败，请重新尝试");
        }
        console.log("this.waitSignInfo is OK===>" + this.waitSignInfo);
    }

    public verifyPwdAndBroadcast() {
        CashboxJs.getInstance().callNativeSignMsgToJs(this.waitSignInfo);
    }

    public nativeSignMsgToJsResult(res: string): void {
        console.log("own_input nativeSignMsgToJsResult===>" + res);
        DiamondUtil.getInstance().sendToChain(res, (status: ExtrinsicStatus) => {
            this.isShowLoading = false;
            if (status.isFinalized) {//完成
                console.log("use balance is finished!===>");
                let txHash = status.asFinalized;
                DiamondUtil.getInstance().loadApiPromise().then((apiPromise) => {
                    apiPromise.query.system.events.at(txHash).then((events) => {
                        let isExecSuccess = false;
                        events.forEach(({phase, event: {data, method, section}}) => {
                            if (section == "contracts" && method == "ContractExecution") {
                                console.log("contracts===>" + data.toJSON());
                                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态", 8000);
                                isExecSuccess = true;
                            }
                            console.log('forEach  =>' + phase.toString() + "||section=>" + `: ${section}.${method}` + data.toString());
                        });
                        if (!isExecSuccess) {
                            ToastUtil.fail("信息上链失败，链上调用出现问题！~", 8000);
                        }
                    });
                });
            }
            if (status.isBroadcast) {//广播
                console.log("status.isBroadcast===>");
                ToastUtil.show("信息上链已完成，可在浏览器查询交易状态");
            }
            if (status.isDropped) {//丢弃
                console.log("status.isDropped===>");
                ToastUtil.fail("信息上链失败，操作已被丢弃");
                return;
            }
            if (status.isInvalid) {//无效
                console.log("status.isInvalid!===>");
                ToastUtil.fail("信息上链失败，无效操作");
                return;
            }
            let tThis = this;
            setTimeout(function () {
                tThis.$router.push({
                    name: 'original_process',
                })
            }, 1000);
        }).catch((reason) => {
            this.isShowLoading = false;
            console.log("Error reason===>" + reason);
            this.$router.replace({
                name: 'original_entry',
            });
            ToastUtil.fail("数据上链中，出现错误！！！请重新尝试操作");
        });
    }
}
